import classNames from 'classnames';
import * as React from 'react';
import {Point} from '../../util/geometry';
import {DocumentTitle} from '../document-title/document-title';
import './main-content.css';

export interface MainContentProps                      //    (?)
	extends React.ComponentPropsWithoutRef<'div'> {      // HTML<div> 
	grabbable?: boolean;                                 //   
	padded?: boolean;                                    // 
	title?: string;                                      //   ?
}

export const MainContent = React.forwardRef<HTMLDivElement, MainContentProps>(       // forwardRef:   ref  
	(props, ref) => {
		const {children, grabbable, title} = props;                 // children, grabbable, title  
		const containerRef = React.useRef<HTMLDivElement>(null);    // containerRef div     ?
		const className = classNames('main-content', {
			padded: props.padded ?? true
		});

		React.useImperativeHandle(
			ref,
			() => containerRef.current as HTMLDivElement
		);

		React.useEffect(() => {                      /**   []      
		                                                 []     
																										 []         */
			if (containerRef.current) {
				containerRef.current.focus();            // containerRef    
			}
		}, []);

		React.useEffect(() => {                                   // grabbable   
			const container = containerRef.current;                // container   div
			let dragScrollStart: Point;
			let dragMouseStart: Point;

			function moveListener(event: PointerEvent) {                           // div 
				if (container) {                                                    // container true
					container.scrollLeft =
						dragScrollStart.left + (dragMouseStart.left - event.clientX);
					container.scrollTop =
						dragScrollStart.top + (dragMouseStart.top - event.clientY);
				}
			}

			function stopGrab(event: PointerEvent) {                      // div 
				if (!container) {                                           // container == false , return
					return;
				}

				container.releasePointerCapture(event.pointerId);               // container  pointerId  
				container.removeEventListener('pointerleave', stopGrab);         // div  
				container.removeEventListener('pointermove', moveListener);      // div 
				container.style.cursor = '';                                    //    
				event.preventDefault();                                         //    
			}

			function upListener(event: PointerEvent) {                     //      
				if (event.button === 2) {                                   //   
					stopGrab(event);                                         // stopGrab 
				}                                                          //  div  
			}

			function downListener(event: PointerEvent) {                 //    
				if (event.button !== 2 || !container) {
					return;
				}

				container.setPointerCapture(event.pointerId);                     //  div   
				container.addEventListener('pointerleave', stopGrab);             
				container.addEventListener('pointermove', moveListener);
				container.addEventListener('pointerup', upListener);
				container.style.cursor = 'grabbing';                           //   
				dragScrollStart = {
					left: container.scrollLeft,
					top: container.scrollTop
				};
				dragMouseStart = {left: event.clientX, top: event.clientY};
				event.preventDefault();
			}

			function ignoreContext(event: Event) {                            //      
				event.preventDefault();
			}

			if (grabbable && container) {                                       // div , div   
				container.addEventListener('pointerdown', downListener);
				container.addEventListener('contextmenu', ignoreContext);         // contextmenu :    .
				return () => {
					container.removeEventListener('pointerdown', downListener);          // 
					container.removeEventListener('contextmenu', ignoreContext);
				};
			}
		}, [grabbable]);


		//     DocumentTitle  HTML  
		return (
			<div className={className} ref={containerRef}>                 
				{title && (
					<>
						<DocumentTitle title={title} />             
						<h1>{title}</h1>
					</>
				)}
				{children}
			</div>
		);
	}
);
