import React from 'react';
import { Component } from 'react';
import './option.css';

/* state  DB    
1.    - int
2.  () - string
3.    - string
4.   - int
5.     - string

+      */

type Modes_props = {
    onCreate:any;
}

class CreateOption extends Component<Modes_props> {
    constructor(props){
        super(props);
    }

    render(){
        return(
            <div className='options-making__container'>
                <form action='' method='post'
                onSubmit={function(e) {
                    e.preventDefault();
                    this.props.onCreate(e.target.option_title.value, e.target.status1.value, e.target.amount_change1.value, e.target.status2.value, e.target.amount_change2.value, e.target.after_story.value);
                }.bind(this)}>
                    
                    <input className='option-mini-title' name='option_title' placeholder='  ' required></input>

                    <div className='mini-line'></div>

                    <div>
                        <select className='select-dropdown' name='status1' required>
                            <option value="">()</option>
                            <option value="health"></option>
                            <option value="money"></option>
                            <option value="satiety"></option>
                            <option value="strength"></option>
                            <option value="agility"></option>
                            <option value="armor"></option>
                            <option value="mental"></option>
                        </select>

                        <select className='select-dropdown' name='amount_change1' required>
                            <option value="">()</option>
                            <option value="1">+1</option>
                            <option value="0">0</option>
                            <option value="-1">-1</option>
                        </select>
                    </div>

                    <div className='mini-line'></div>

                    <div>
                        <select className='select-dropdown' name='status2' required>
                            <option value="">(2)</option>
                            <option value="null">()</option>
                            <option value="health"></option>
                            <option value="money"></option>
                            <option value="satiety"></option>
                            <option value="strength"></option>
                            <option value="agility"></option>
                            <option value="armor"></option>
                            <option value="mental"></option>
                        </select>

                        <select className='select-dropdown' name='amount_change2' required>
                            <option value="0">0</option>
                            <option value="1">+1</option>
                            <option value="-1">-1</option>
                        </select>
                    </div>

                    <div className='mini-line'></div>

                    
                    <textarea placeholder='      ' className='option-mini-textarea' name="after_story" id="" cols={30} rows={10} required></textarea>
                    
                    <input className='option-submit-btn' type="submit" value=""></input>
                </form>
            </div>
        )
    }
}

export default CreateOption;