


import classNames from 'classnames';                                     //  class  
import {deviceType} from 'detect-it';                                    //   
import * as React from 'react';
import {DraggableCore, DraggableCoreProps} from 'react-draggable';       //     
import {useTranslation} from 'react-i18next';
import {Passage, TagColors} from '../../store/stories';
import {CardContent} from '../container/card';
import {SelectableCard} from '../container/card/selectable-card';
import {TagStripe} from '../tag/tag-stripe';
import './passage-card.css';

export interface PassageCardProps {
	onEdit: (passage: Passage) => void;                       // Passage interface    void   
	onDeselect: (passage: Passage) => void;                   //  
	onDragStart?: DraggableCoreProps['onStart'];             // DraggableCoreProps onStart   
	onDrag?: DraggableCoreProps['onDrag'];                   // DraggableCoreProps onDrag   
	onDragStop?: DraggableCoreProps['onStop'];               // DraggableCoreProps onStop   
	onSelect: (passage: Passage, exclusive: boolean) => void;  // Passage  boolean   void   
	passage: Passage;
	tagColors: TagColors;
}

// Needs to fill a large-sized passage card.
const excerptLength = 400;

export const PassageCard: React.FC<PassageCardProps> = React.memo(props => {
	const {
		onDeselect,
		onDrag,
		onDragStart,
		onDragStop,
		onEdit,
		onSelect,
		passage,
		tagColors
	} = props;
	const {t} = useTranslation();                  // t('...')   .
	/**
	 * passage.select, passage.tags.length, passage.text  ,
	 * classNames(). 
	 * passage-card   .
	 * empty passage.text , passage.tags.length 0  .
	 * selected passage.selected true  
	 */
	const className = React.useMemo(                                 
		() =>
			classNames('passage-card', {
				empty: passage.text === '' && passage.tags.length === 0,
				selected: passage.selected
			}),
		[passage.selected, passage.tags.length, passage.text]
	);
	const container = React.useRef<HTMLDivElement>(null);     // div   current  .     
	/**
	 * passage.text, t  
	 *  , passage   excerpt .
	 *   , placeholder 
	 */
	const excerpt = React.useMemo(() => {
		if (passage.text.length > 0) {
			return passage.text.substring(0, excerptLength);
		}

		return (
			<span className="placeholder">
				{t(
					deviceType === 'touchOnly'
						? 'components.passageCard.placeholderTouch'
						: 'components.passageCard.placeholderClick'
				)}
			</span>
		);
	}, [passage.text, t]);
	/**
	 * passage.height, passage.left, passage.top, passage.width  ,
	 * style height, left, top, width  
	 */
	const style = React.useMemo(
		() => ({
			height: passage.height,
			left: passage.left,
			top: passage.top,
			width: passage.width
		}),
		[passage.height, passage.left, passage.top, passage.width]
	);
	/**
	 * shift+, ctrl+ passage card    
	 */
	const handleMouseDown = React.useCallback(
		(event: MouseEvent) => {
			if (event.shiftKey || event.ctrlKey) {
				if (passage.selected) {
					onDeselect(passage);
				} else {
					onSelect(passage, false);
				}
			} else if (!passage.selected) {
				onSelect(passage, true);
			}
		},
		[onDeselect, onSelect, passage]
	);
	/**
	 * passage    onEdit 
	 */
	const handleEdit = React.useCallback(
		() => onEdit(passage),
		[onEdit, passage]
	);
	/**
	 * passage    onSelect 
	 */
	const handleSelect = React.useCallback(
		(value: boolean, exclusive: boolean) => {
			onSelect(passage, exclusive);
		},
		[onSelect, passage]
	);

	return (
		<DraggableCore
			nodeRef={container}
			onMouseDown={handleMouseDown}
			onStart={onDragStart}
			onDrag={onDrag}
			onStop={onDragStop}
		>
			<div className={className} ref={container} style={style}>
				<SelectableCard                                            //   passage card
					highlighted={passage.highlighted}
					label={passage.name}
					onDoubleClick={handleEdit}             //   handleEdit 
					onSelect={handleSelect}                //    handleSeelct 
					selected={passage.selected}
				>
					<TagStripe tagColors={tagColors} tags={passage.tags} />
					<h2>{passage.name}</h2>
					<CardContent>{excerpt}</CardContent>
				</SelectableCard>
			</div>
		</DraggableCore>
	);
});

PassageCard.displayName = 'PassageCard';